.CodeGraphInformationPane {
  border: 1px dotted gray;
  padding: 5px;
  font-size: .92rem;
  overflow: auto;
}

.CodeGraphInformationPane .empty-info-pane {
  padding: 30px;
}

.CodeGraphInformationPane > div {
  margin-top: 1em;
  margin-bottom: 1em;
}

.CodeGraphInformationPane .code-name {
  font-size: 1rem;
  font-weight: bold;
  display: inline-block;
}

.CodeGraphInformationPane .code-introduced {
  margin-left: 4px;
  display: inline-block;
}

.CodeGraphInformationPane .kingdom-name {
  background-color: #c8ffe6;
  font-size: 1rem;
  font-weight: bold;
}

.CodeGraphInformationPane .kingdom-name .kingdom-name-label {
  font-weight: normal;
}

.CodeGraphInformationPane .kingdom-description {
  background-color: #c8ffe6;
}

.CodeGraphInformationPane .code-link, .CodeGraphInformationPane .domain-link, .CodeGraphInformationPane .kingdom-link {
  padding-left: auto;
  display: block;
}

.CodeGraphInformationPane .code-relations .code-relation-type {
  font-weight: bold;
  display: block;
}

.CodeGraphInformationPane .code-relations > ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
  display: inline;
}

.CodeGraphInformationPane .code-relations > ul > li {
  list-style: none;
  display: inline;
}

.CodeGraphInformationPane .code-relations > ul > li:before {
  content: " • ";
}

.CodeGraphInformationPane .code-relations > ul > li:first-child:before {
  content: "";
}

.CodeGraphInformationPane .endnotes {
  color: #505064;
  font-size: .85rem;
}

.CodeGraphInformationPane .domain-name {
  font-weight: bold;
}

.CodeGraphInformationPane .llm-html-error {
  color: #c80000;
  font-weight: bold;
}

.CodeGraphInformationPane .llm-html-error > pre {
  white-space: pre-wrap;
}

.EczCodeGraphComponent {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  --side-panel-width: 260px;
  grid-template-columns: calc(100% - var(--side-panel-width)  - 3px) var(--side-panel-width);
  grid-template-rows: 100%;
  gap: 3px;
  display: grid;
  overflow: hidden;
}

@media screen and (max-width: 500px) {
  .EczCodeGraphComponent {
    grid-template-rows: 80% 20%;
    grid-template-columns: 100%;
  }

  .EczCodeGraphComponent .EczCodeGraphComponent_SidePanel > div {
    min-height: 200px;
    overflow: hidden;
  }
}

@media screen and (min-width: 800px) {
  .EczCodeGraphComponent {
    --side-panel-width: 320px;
  }
}

@media screen and (min-width: 1000px) {
  .EczCodeGraphComponent {
    --side-panel-width: 390px;
  }
}

@media screen and (min-width: 1500px) {
  .EczCodeGraphComponent {
    --side-panel-width: 450px;
  }
}

.EczCodeGraphComponent_CyPanel {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
}

.EczCodeGraphComponent_Cy {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
}

.EczCodeGraphComponent_SidePanel {
  height: 100%;
  z-index: 100;
  background-color: #fff;
  grid-template-rows: 0fr 1fr;
  gap: 10px;
  margin: 0;
  padding: 0;
  display: grid;
  overflow: auto;
}

.EczCodeGraphControlsComponent {
  font-size: .9rem;
}

